import axios from "axios";
import { useContext, useEffect, useRef, useState } from "react";
import {
  MessageContext,
  handleSuccessMessage,
  handleErrorMessage,
} from "../contexts/MessageContext";

function ProductModal({ closeProductModal, getProducts, type, tempProduct }) {
  const saveBtnRef = useRef(null);
  const firstImgRef = useRef(null);
  //  token
  const token = document.cookie
    .split("; ")
    .find((row) => row.startsWith("hextoken="))
    ?.split("=")[1];
  axios.defaults.headers.common["Authorization"] = token;

  const [tempData, setTempData] = useState({
    title: "",
    category: "",
    origin_price: 0,
    price: 0,
    unit: "",
    description: "",
    content: "",
    is_enabled: 1,
    imageUrl: "",
    imagesUrl: ["", "", "", "", ""],
  });

  const [, dispatch] = useContext(MessageContext);

  useEffect(() => {
    if (type === "create") {
      setTempData({
        title: "",
        category: "",
        origin_price: 0,
        price: 0,
        unit: "",
        description: "",
        content: "",
        is_enabled: 1,
        imageUrl: "",
        imagesUrl: ["", "", "", "", ""],
      });
    } else if (type === "edit") {
      setTempData(tempProduct);
    }
  }, [type, tempProduct]);

  const handleChange = (e) => {
    const { value, name } = e.target;
    if (["price", "origin_price"].includes(name)) {
      setTempData({
        ...tempData,
        [name]: Number(value),
      });
    } else if (name === "is_enabled") {
      setTempData({
        ...tempData,
        [name]: +e.target.checked, // boolean
      });
    } else {
      setTempData({
        ...tempData,
        [name]: value,
      });
    }
  };
  const hadleChangeImages = (e) => {
    const index = parseInt(e.target.name);
    setTempData({
      ...tempData,
      imagesUrl: tempData.imagesUrl.map((item, i) =>
        i === index ? e.target.value : item,
      ),
    });
  };

  const submit = async () => {
    saveBtnRef.current.setAttribute('disabled', '');
    try {
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = "post";
      if (type === "edit") {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`;
        method = "put";
      }
      const res = await axios[method](api, {
        data: tempData,
      });
      handleSuccessMessage(dispatch, res);
      closeProductModal();
      getProducts();
    } catch (error) {
      handleErrorMessage(dispatch, error);
    }
    saveBtnRef.current.removeAttribute('disabled');
  };
  const uploadFile = async (e) => {
    const { name, files } = e.target;
    const file = files[0];
    if (!file) {
      return;
    }

    const formData = new FormData();
    formData.append("file-to-upload", file);
    try {
      const res = await axios.post(
        `/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`,
        formData,
        {
          headers: {
            authorization: token,
          },
        },
      );
      if (e.target.name === "imageUrl") {
        setTempData({
          ...tempData,
          [name]: res.data.imageUrl,
        });
      } else {
        const index = parseInt(e.target.name);
        setTempData({
          ...tempData,
          imagesUrl: tempData.imagesUrl.map((item, i) =>
            i === index ? res.data.imageUrl : item,
          ),
        });
      }
    } catch (error) {
      handleErrorMessage(dispatch, error);
    }
  };

  useEffect(() => {
    const inputs = document.querySelectorAll('input[type="number"]');
    const handleWheel = (e) => {
      e.preventDefault();
    };
    inputs.forEach((input) => {
      //  input  passive 
      input.addEventListener("wheel", handleWheel, { passive: false });
    });

    return () => {
      inputs.forEach((input) => {
        input.removeEventListener("wheel", handleWheel);
      });
    };
  }, []);

  return (
    <div
      className="modal fade"
      tabIndex="-1"
      id="productModal"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              {type === "create" ? "" : ``}
            </h1>
            <button
              type="button"
              className="btn-close"
              aria-label="Close"
              onClick={() => {
                closeProductModal();
                firstImgRef.current.value = '';
                if (Object.keys(tempProduct).length !== 0) {
                  setTempData(tempProduct);
                }
              }}
            />
          </div>
          <div className="modal-body">
            <div className="row">
              <div className="col-sm-4">

                <div className="form-group mb-5">
                  <label className="w-100 " htmlFor="image">
                    
                    <input
                      type="text"
                      name="imageUrl"
                      id="image"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.imageUrl}
                    />
                  </label>
                  <div className="form-group ">
                    <input
                      type="file"
                      id="customFile"
                      className="form-control"
                      name="imageUrl"
                      onChange={(e) => uploadFile(e)}
                      ref={firstImgRef}
                    />
                  </div>
                  {tempData.imageUrl && (
                    <img className="w-100" src={tempData.imageUrl} alt="primary-image" />
                  )}
                </div>
                {tempData?.imagesUrl?.map((imageurl, i) =>
                (
                  <div key={i} className="form-group mb-5">
                    <label className="w-100" htmlFor="image">
                       {i + 1}
                      <input
                        type="text"
                        name={i}
                        id="image"
                        placeholder=""
                        className="form-control"
                        onChange={hadleChangeImages}
                        value={imageurl}
                      />
                    </label>
                    <div className="form-group ">
                      <input
                        type="file"
                        id="customFile"
                        className="form-control"
                        name={i}
                        onChange={(e) => uploadFile(e)}
                      />
                    </div>
                    {tempData?.imagesUrl?.[i] && (
                      <img
                        className="w-100"
                        src={tempData?.imagesUrl?.[i]}
                        alt={`other-image-${i}`}
                      />
                    )}
                  </div>
                )
                )}
              </div>
              <div className="col-sm-8">
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="title">
                    
                    <input
                      type="text"
                      id="title"
                      name="title"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.title}
                    />
                  </label>
                </div>
                <div className="row">
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="category">
                      
                      <select
                        className="form-select"
                        aria-label="Default select example"
                        name="category"
                        onChange={handleChange}
                        value={tempData.category}
                      >
                        <option value="" disabled>
                          
                        </option>
                        <option value="apple">apple</option>
                        <option value="others">others</option>
                        <option value="gameConsole">gameConsole</option>
                        <option value="controller">controller</option>
                      </select>
                    </label>
                  </div>
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="unit">
                      
                      <input
                        type="unit"
                        id="unit"
                        name="unit"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.unit}
                      />
                    </label>
                  </div>
                </div>
                <div className="row">
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="origin_price">
                      
                      <input
                        type="number"
                        id="origin_price"
                        name="origin_price"
                        placeholder=""
                        className="form-control noscroll"
                        onChange={handleChange}
                        value={tempData.origin_price}
                      />
                    </label>
                  </div>
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="price">
                      
                      <input
                        type="number"
                        id="price"
                        name="price"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.price}
                      />
                    </label>
                  </div>
                </div>
                <hr />
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="description">
                    
                    <textarea
                      type="text"
                      id="description"
                      name="description"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.description?.trim()}
                    />
                  </label>
                </div>
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="content">
                    
                    <textarea
                      type="text"
                      id="content"
                      name="content"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.content}
                      style={{ height: "300px" }}
                    />
                  </label>
                </div>
                <div className="form-group mb-2">
                  <div className="form-check">
                    <label
                      className="w-100 form-check-label"
                      htmlFor="is_enabled"
                    >
                      
                      <input
                        type="checkbox"
                        id="is_enabled"
                        name="is_enabled"
                        placeholder=""
                        className="form-check-input"
                        onChange={handleChange}
                        checked={!!tempData.is_enabled}
                      />
                    </label>
                  </div>
                </div>
                <div className="modal-footer mt-5">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    onClick={() => {
                      closeProductModal();
                      firstImgRef.current.value = '';
                      if (!Object.keys(tempProduct).length === 0) {
                        setTempData(tempProduct);
                      }
                    }}
                  >
                    
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary"
                    onClick={submit}
                    ref={saveBtnRef}
                  >
                    
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default ProductModal;
