import { useEffect, useState } from "react";
import moment from "moment/moment";

function QueryModal({ closeOrderModal, tempOrder }) {
  const [progress, setProgress] = useState(0);
  const [tempData, setTempData] = useState({
    create_at: "",
    id: "",
    is_paid: "",
    message: "",
    products: [
      {
        id: "",
        product_id: "",
        qty: "",
      },
    ],
    user: {
      address: "",
      email: "",
      name: "",
      tel: "",
    },
    num: 2,
  });
  useEffect(() => {
    setTempData({
      ...tempOrder,
      create_at: moment.unix(tempOrder?.create_at).format("ll"),
    });
    const { status } = tempOrder;
    switch (status) {
      case "":
        setProgress(0);
        break;
      case "":
        setProgress(33);
        break;
      case "":
        setProgress(66);
        break;
      case "":
        setProgress(99);
        break;

      default:
        break;
    }
  }, [tempOrder]);

  return (
    <div
      className="modal fade"
      tabIndex="-1"
      id="queryModal"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              {` ${tempData?.id}`}
            </h1>
            <button
              type="button"
              className="btn-close"
              aria-label="Close"
              onClick={closeOrderModal}
            />
          </div>

          <div className="modal-body">
            <div>{tempData?.status}</div>
            <table
              className="table mt-3 mb-0 table-bordered border-primary small-table"
              style={{
                tableLayout: "fixed",
                fontSize: "13px",
              }}
            >
              <tbody>
                <tr className="text-center ">
                  <td className={progress >= 33 ? "table-primary" : ""}>
                    
                  </td>
                  <td className={progress >= 66 ? "table-primary" : ""}>
                    
                  </td>
                  <td className={progress >= 99 ? "table-primary" : ""}>
                    
                  </td>
                </tr>
              </tbody>
            </table>
            <div className="mb-3 mt-3 row">
              <span className="col-sm-2  col-form-label"></span>
              <div className="col-sm-10  ">
                <input
                  type="text"
                  readOnly
                  className="form-control-plaintext"
                  id="staticEmail"
                  defaultValue={tempData?.create_at}
                />
              </div>
            </div>
            <div className="mb-3 row">
              <span className="col-sm-2 col-form-label">Email</span>
              <div className="col-sm-10">
                <input
                  type="email"
                  readOnly
                  className="form-control-plaintext"
                  id="staticEmail"
                  defaultValue={tempData?.user?.email}
                />
              </div>
            </div>
            <div className="mb-3 row">
              <span className="col-sm-2 col-form-label"></span>
              <div className="col-sm-10">
                <input
                  type="text"
                  readOnly
                  className="form-control-plaintext"
                  id="staticEmail"
                  defaultValue={tempData?.user?.name}
                />
              </div>
            </div>
            <div className="mb-3 row">
              <span className="col-sm-2 col-form-label"></span>
              <div className="col-sm-10">
                <input
                  type="text"
                  readOnly
                  className="form-control-plaintext"
                  defaultValue={tempData?.user?.address}
                />
              </div>
            </div>
            <div className="mb-3 row">
              <span className="col-sm-2 col-form-label"></span>
              <div className="col-sm-10">
                <input
                  type="text"
                  readOnly
                  className="form-control-plaintext"
                  defaultValue={tempData?.is_paid ? "" : ""}
                />
              </div>
            </div>

            <div className="mb-3 row">
              <span className="col-sm-2 col-form-label"></span>
              <div className="col-sm-10">
                <textarea
                  name=""
                  id=""
                  cols="30"
                  readOnly
                  className="form-control-plaintext"
                  defaultValue={tempData?.user?.message}
                />
              </div>
            </div>
            {tempData?.products && (
              <table className="table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {Object.values(tempData?.products).map((cart) => (
                    <tr key={cart?.id}>
                      <td>{cart?.product?.title}</td>
                      <td>{cart?.qty}</td>
                    </tr>
                  ))}
                </tbody>
                <tfoot>
                  <tr>
                    <td className="border-0 text-end"></td>
                    <td className="border-0">${tempData?.total}</td>
                  </tr>
                </tfoot>
              </table>
            )}
          </div>
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={closeOrderModal}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
export default QueryModal;
