import { useNavigate, useOutletContext, Link } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { Textarea, Input } from "../../components/FontElements";

function Checkout() {
  const { cartData } = useOutletContext();

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onTouched",
  });

  const navigate = useNavigate();

  const onSubmit = async (data) => {
    const { name, email, tel, address, message } = data;
    const form = {
      data: {
        user: {
          name,
          email,
          tel,
          address,
          message,
        },
      },
    };
    const res = await axios.post(
      `/v2/api/${process.env.REACT_APP_API_PATH}/order`,
      form,
    );
    navigate(`/success/${res.data.orderId}`);
  };

  return (
    <div className="bg-light pt-5 pb-7 min-vh-100">
      <div className="container">
        <div className="row justify-content-center flex-md-row flex-column-reverse">
          <form className="col-md-6" onSubmit={handleSubmit(onSubmit)}>
            <div className="bg-white p-4">
              <h4 className="fw-bold"></h4>
              <div>
                <div className="mb-2">
                  <Input
                    placeholder="email"
                    id="email"
                    labelText="Email"
                    type="email"
                    errors={errors}
                    register={register}
                    rules={{
                      required: "Email ",
                      pattern: {
                        value: /^\S+@\S+$/i,
                        message: "Email ",
                      },
                    }}
                  />
                </div>

                <div className="mb-2">
                  <Input
                    placeholder=""
                    id="name"
                    type="text"
                    errors={errors}
                    labelText=""
                    register={register}
                    rules={{
                      required: "",
                      maxLength: {
                        value: 10,
                        message: " 10",
                      },
                    }}
                  />
                </div>
                <div className="">
                  <Input
                    placeholder=""
                    id="tel"
                    labelText=""
                    type="tel"
                    errors={errors}
                    register={register}
                    rules={{
                      required: "",
                      minLength: {
                        value: 6,
                        message: " 6 ",
                      },
                      maxLength: {
                        value: 12,
                        message: " 12 ",
                      },
                    }}
                  />
                </div>
                <div className="">
                  <Input
                    placeholder=""
                    id="address"
                    labelText=""
                    type="address"
                    errors={errors}
                    register={register}
                    rules={{
                      required: "",
                    }}
                  />
                </div>
                <div className="">
                  <Textarea
                    placeholder=""
                    id="message"
                    labelText=""
                    type="Textarea"
                    errors={errors}
                    register={register}
                    rules={{
                      maxLength: {
                        value: 100,
                        message: "100",
                      },
                    }}
                  />
                </div>
              </div>
            </div>

            <div className="checkout-button d-flex flex-column-reverse flex-md-row mt-4 justify-content-between align-items-md-center align-items-md-end w-100">
              <Link to="/products" className="text-dark mt-md-0 mt-3">
                <i className="fas fa-chevron-left me-2" /> 
              </Link>
              <button
                type="submit"
                className="btn btn-dark py-3 px-7 rounded-0"
              >
                
              </button>
            </div>
          </form>
          <div className="col-md-4">
            <div className="border p-4 mb-4">
              <h4 className="mb-4"></h4>
              {cartData?.carts?.map((item) => (
                <div className="d-flex m-3 mt-5" key={item.id}>
                  <div>
                    <img
                      src={item.product.imageUrl}
                      alt={item.product.title}
                      className="me-2 "
                      style={{
                        width: "48px",
                        height: "48px",
                        objectFit: "cover",
                      }}
                    />
                  </div>
                  <div className="w-100">
                    <div className="d-flex justify-content-between fw-bold">
                      <p className="mb-0 ">{item.product.title}</p>
                      <p className="mb-0">x{item.qty}</p>
                    </div>
                    <div
                      className="d-flex 
                                            flex-row 
                                            flex-lg-row
                                            justify-content-between
                                            flex-md-column
                                            mt-auto checkout-order-data"
                    >
                      {/*
                                                 flex-row 768pxrow()
                                                 flex-lg-row 992pxrow()
                                                 flex-md-column 768~992pxcol()
                                                 */}
                      <p className="text-muted mb-0">
                        <small>NT${item.product.price}</small>
                      </p>
                      <p
                        className={`${item.total !== item.final_total
                          ? "text-secondary fs-7 text-decoration-line-through"
                          : ""  }mb-0`}
                      >
                        NT${item.total}
                      </p>
                    </div>
                    <div
                      className={`${item.total === item.final_total ? "d-none" : ""
                        } text-end`}
                    >
                      NT${item.final_total}
                    </div>
                  </div>
                </div>
              ))}

              <hr />
              <div className="d-flex justify-content-between mt-4">
                <p className="mb-0 h4 fw-bold"></p>
                <p className="mb-0 h4 fw-bold">NT${cartData.final_total}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Checkout;
